@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(243, 215, 215);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --primary: #a097da;
  --warning: #ffd028;
  --danger: #eb3f27;
  --success: #75fa83;
  --white: #fdfdfd;
  --dark: #181717;
}

.btn {
  font-family: "Prompt", sans-serif;
  font-size: 15px;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  box-shadow: 0 4px 8px 0 grey;
}

.btn:hover {
  transform: translateY(-3px);
  background-color: rgb(221, 199, 231);
}

/* Button Styles */

.btn--primary--solid {
  background-color: #a097da;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: 2px solid rgb(142, 120, 223);
}

.btn--primary--outline {
  background-color: transparent;
  color: #a097da;
  color: var(--primary);
  border: 2px solid #a097da;
  border: 2px solid var(--primary);
}

.btn--medium {
  height: 40px;
  font-size: 18px;
}

.search--primary {
  position: absolute;
  top: 355px;
  left: 575px;
}

.search--primary {
  height: 30px;
  width: 50px;
}

.App {
  text-align: center;
  font-family: Raleway;
  font-weight: 300;
}

.App-header {
  height: 100px;
  width: auto;
  margin: 0;
  line-height: 50%;
}

.titles {
  height: 100px;
  background: rgb(219, 155, 134);
  background: radial-gradient(
    circle,
    rgb(215, 129, 226) 12%,
    rgba(224, 90, 131, 0.65) 85%
  );
  border: 2px grey;
  box-shadow: 0 4px 8px 0 grey;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 2%;
}

.App-title {
  font-family: Prompt, monospace;
  color: white;
  font-size: 60px;
  text-align: center;
  vertical-align: middle;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke: purple;
}

.sub-title {
  font-family: Raleway, monospace;
  color: white;
  font-size: 15px;
}

.Select-diff {
  border: 2px solid grey;
  border-radius: 25px;
  height: 200px;
  width: 700px;
  box-shadow: 0 4px 8px 0 grey;
  background-color: rgb(214, 187, 206);
}

.Select-diff:hover {
  box-shadow: 0 8px 8px 0 white;
}

.Options {
  color: grey;
  align-content: center;
}

.outer {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.middle {
  display: table-cell;
  vertical-align: middle;
}

.inner {
  margin-left: auto;
  margin-right: auto;
  width: 700px;
}

